import { Col, Row } from 'antd';
import Fade from 'react-reveal/Fade';
import Rotate from 'react-reveal/Rotate';

import styles from './styles.module.scss';
import useTyped from '../../hooks/useTyped';
import useLottie from '../../hooks/useLottie';

const strings = [
  '看电影，',
  '轻小说，',
  '动画，',
  '小动物，',
  '粤语歌，',
  '甜食，',
];

export default function Home() {
  const el = useTyped(strings);
  const catLottieRef = useLottie(
    'https://assets10.lottiefiles.com/packages/lf20_tr1pjkop.json',
  );
  const arrowLottieRef = useLottie(
    'https://assets4.lottiefiles.com/private_files/lf30_vejj8cpm.json',
  );

  const next = () => {
    const $about = document.querySelector('#about');
    if ($about) $about.scrollIntoView({ behavior: 'smooth' });
  };

  return (
    <section id='home' className={styles.home}>
      <Row className={styles.content}>
        <Col span={24} md={15} className={styles.intro}>
          <p>Hi!</p>
          <p>我是Chris。</p>
          <p>一个前端开发。</p>
          <p>
            喜欢
            <span className={styles.like} ref={el} />
          </p>
          <p>喜欢有趣。</p>
        </Col>

        <Col span={25} md={9} className={styles.ballWrapper}>
          <Rotate>
            <div className={styles.ball}>
              <div ref={catLottieRef} />
            </div>
          </Rotate>
        </Col>

        <Col className={styles.next} span={24}>
          <Fade top>
            <div className={styles.handDown}>
              <div ref={arrowLottieRef} onClick={next} />
            </div>
          </Fade>
        </Col>
      </Row>
    </section>
  );
}
